input#abrir-cerrar {
    visibility:hidden;
    position: absolute;
    top: -9999px;
}


label[for="abrir-cerrar"] {
    cursor:pointer;
    padding: 1rem;
    background-color:#333;
    color:#fff;
    display:inline-block;
    width:100%;

}

.cerrar {
    display:none;
}

#contenido {
    margin-left:0;
}

input#abrir-cerrar:checked ~ #Menu {
    width:300px;
}

input#abrir-cerrar:checked + label[for="abrir-cerrar"], input#abrir-cerrar:checked ~ #contenido {
    margin-left:300px;
    transition: margin-left .4s;
}


input#abrir-cerrar:checked + label[for="abrir-cerrar"] .cerrar {
    display:inline;
}

input#abrir-cerrar:checked + label[for="abrir-cerrar"] .abrir {
    display:none;
}

.Menu {
    position: fixed;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #00324b;
    overflow-x: hidden;
    transition: 0.4s;
    padding: 1rem 0;
    box-sizing:border-box;
}

.Menu__ul, .Menu__li{
    margin:0;
    padding:0;
    list-style:none inside;
}

.Menu__ul {
    margin: 4rem auto;
    display: block;
    width: 80%;
    min-width:200px;
}

.Menu__Link {
    display: block;
    font-size: 120%;
    color: #eee;
    text-decoration: none;
    
}

.Menu__Link:hover, .Menu__Link--active{
    color:#fff;
    background-color: #f90;

}

